.love__container {
    position: fixed;
    bottom: 1em;
    right: 1em;
    color: whitesmoke;
    z-index: 99999;
    font-size: .725rem;
}

.love__container__span {
    text-decoration: none;
    padding: .25em .75em;
    background-color: rgba(255, 255, 255, 0.068);
    border: 1px rgba(255, 255, 255, 0.062) solid;
    border-radius: .5em;
    color: #5eead4;
    font-family: "Gosh";
    font-size: 1.15em;
}