.scan__container {
    top: 0;
    left: 0;
    width: 250px;
    min-height: 100vh;
    max-height: 100%;
    height: fit-content;
    position: fixed;
    background-color: rgb(14, 14, 14);
    display: flex;
    justify-content: start;
    flex-direction: column;
    z-index: 99;
    overflow-y: scroll;
    bottom: 0;
    border-right: 1px rgba(255, 255, 255, 0.185) solid;
    transition: .15s;
}
.arroww {
    transition: .25s ease-in-out;
}
.sonic_x__container {

    position: absolute;
    bottom: 0; 
    padding: 1em;
    width: 100%;
}
.sonic_x {
    width: 100%;
}
input[type=range] {
    -webkit-appearance: none;
    appearance: none;
    
    background-color: rgba(255, 255, 255, 0.055);
    outline: none;
    height: 10px;
    border-radius: 1em;
}
input[type=range]::-webkit-slider-runnable-track {
    padding: .5em;
    height: fit-content;
    cursor: grab;

  }
input[type=range]::-webkit-slider-runnable-track:active {
    cursor: grabbing;
  }
input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
  appearance: none;
    height: 2em;
    width: 10px;
    background-color: #a855f7;
    border: #b36bf7 2px solid;
    border-radius: 1em;

  }

  .sonic_x__label {
    color: rgba(245, 245, 245, 0.74);
    font-weight: 100;
    margin-bottom: .5em;
  }
.scan__container__arrow {
    overflow: visible;
    width: 40px;
    height: 70px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    position: fixed;
    background-color: rgb(12, 12, 12);
    display: flex;
    justify-content: center;
    align-items: center;
    left: 249px;
    top: 50vh;
    transform: translate(0%, -50%);
    border: 1px rgba(255, 255, 255, 0.185) solid;
    z-index: 100;
    cursor: pointer;
    transition: .15s ease-in-out;
}
.scan__container__arrow:hover svg {
    fill: #a855f7;
}
.scan__container__arrow:hover {
    border: 1px #a955f79c solid;
}
.scan__container__hide {
    content: "";
    transition: .15s ease-in-out;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 249px;
    height: 50px;
    background: rgb(12,12,12);
    background: linear-gradient(0deg, rgba(12,12,12,1) 21%, rgba(13,13,13,0.6110819327731092) 64%, rgba(13,13,13,0) 100%);
    pointer-events: none;
}
.scan__container::-webkit-scrollbar {
    width: 0px;
}
.scan__container__item {
    border-bottom: 1px rgba(255, 255, 255, 0.068) solid;
    padding: 1em;
    color: rgba(255, 255, 255, 0.712);

}
.scan__container__item:hover {
    border-bottom: 1px #a955f79c solid;
    padding: 1em;
    background-color: rgba(255, 255, 255, 0.082);   
    cursor: pointer;
    box-shadow: inset rgba(0, 0, 0, 0.397) 0 0 7px;
    
}