.all_container {
    background-color: rgb(12, 12, 12);
    padding-top: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1em;
    width: 100%;
}
.vol__container {
    background-color: rgb(12, 12, 12);
    padding-top: 5em;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 1em;
    width: 80%;
}

.image__scan {
    cursor: pointer;
    width: 100%;
}

.bookmark__icon {
    position: absolute;
    font-size: 5em;
    fill: #a855f7;
    pointer-events: none;
    user-select: none;
    opacity: 1;
    transform : translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.767);
    backdrop-filter: blur(2px);
    padding: 7px;
    border-radius: 50%;
    box-shadow: rgba(82, 82, 82, 0.144) 0 2px 7px;
    z-index: 1;
}
.booped {
    pointer-events: none;
    user-select: none;

    width: 250px;
    height: 250px;
    border-radius: 50%;
    background-color: #8b5cf6;
    opacity: 1;
    position: absolute;
    animation: booped .5s ease-out forwards;
    transform: translate(-50%, -50%) scale(0);
    z-index: 0;

}

@keyframes booped {
    to {
        opacity: 0;
        transform: translate(-50%, -50%) scale(1);
    }
}
.bookmark__container {
    position: absolute;
}