@font-face {
  font-family: "Charle";
  src: url("../assets/fonts/PPCharlevoix-Regular.woff2");
  font-weight: normal;
}
@font-face {
  font-family: "Charle";
  src: url("../assets/fonts/PPCharlevoix-Bold.woff2");
  font-weight: bold;
}
@font-face {
  font-family: "Charle";
  src: url("../assets/fonts/PPCharlevoix-Black.woff2");
  font-weight: 900;
}
@font-face {
  font-family: "Charle";
  src: url("../assets/fonts/PPCharlevoix-Thin.woff2");
  font-weight: 100;
}
@font-face {
  font-family: "Gosh";
  src: url("../assets/fonts/PPGoshaSans-Regular.ttf");
  font-weight: normal;
}
@font-face {
  font-family: "Gosh";
  src: url("../assets/fonts/PPGoshaSans-Ultrabold.otf");
  font-weight: 900;
}

*, ::before, ::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Charle"
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
