::-webkit-scrollbar {
    width: 10px;
    background-color: rgb(12, 12, 12);
    outline: rgba(255, 255, 255, 0.397) 1px solid;
    box-shadow: rgb(255, 255, 255) -px 0px 7px;
}
::-webkit-scrollbar-thumb {
    background-color: rgba(245, 245, 245, 0.349);
    border-radius: 99px;
    width: 5px;
}

.nav__container {
    position: fixed;
    top: 0;
    transition: .25s ease-in-out;
    display: flex;
    width: 100vw;
    padding: 1em;
    justify-content: center;
    color: whitesmoke;
    z-index: 2;
    gap: 1em;
    box-shadow: black 2px 0 7px;
    background-color: rgba(0, 0, 0, 0.815);
    backdrop-filter: blur(3px);
}
.nav__container_item {
    cursor: pointer;
}
.nav__container_item {
    text-decoration: none;
    color: whitesmoke;
    font-family: "Gosh";
    font-weight: 900;
    transition: .15s;
    font-size: 1.25rem;
}

.nav__container_item:hover {
    color: #c084fc
}
.nav__container__underline {
    background-color: #c084fc;
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0;
    transition: .25s ease-out;
}