.App {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(12, 12, 12);
  height: 100vh;
  width: 100%;
  color: whitesmoke;
  flex-direction: column;
  gap: 1em;
  position: relative;
  z-index: 1;
  overflow: hidden;

}
h1 {
  font-family: "Gosh";
  font-weight: 900;
  font-size: 2.5rem;
}
.App::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: -1;
  background-image: url("../assets/background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  filter: blur(2px) brightness(.075);
  transform: scale(1.05);
}

.container {
  padding: 1em;
  width: fit-content;
  color: whitesmoke;
}

.dark {
  background-color: rgb(12, 12, 12);
}
.light {
  background-color: rgb(230, 230, 230);
}
.video__container {
  max-width: 1000px;
  width: 90%;
}
.video__container__video {
  aspect-ratio: 16/9;
  width: 100%;
}
.flag__container {
  display: flex;
  gap: 1em;
}
input[type="radio"] {
  display: none;
}
input[type="radio"] +label > img {
  transition: .15s;
}
/* input[type="radio"]:checked + label{
  outline: rgb(255, 255, 255) 2px solid;
} */
input[type="radio"]:checked  + label > img {
  outline: rgba(255, 255, 255, 0.808) 2px solid;
  width: 3em!important;
  transition: .15s;
  opacity: 1;
}
input[type="radio"]:not(checked)  + label > img {
  opacity: .5;
  transition: .15s;
}
input[type="radio"]:hover  + label > img {
  outline: rgba(255, 255, 255, 0.733) 1px solid;
  width: 2.5em!important;
  transition: .15s;
}
.flag__container {
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#viewer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 5em 0;
  gap: 1em;
  background-color: rgb(12, 12, 12);
}
